<template lang="pug">
  .container
    title lista de tareas
    .row
      .col-10
            .jumbotron.mt-4
                //-Lista de tareas
                div
                    h1.text-center.mb-3 Lista de tareas

                    .form-group
                        input.form-control(v-model="content" @keyup.enter="agregarTarea")
                        ul.list-group
                            li(v-for="(item, index) in nuevaTarea").list-group-item.d-flex.justify-content-between.px-5
                                span(:class="item.estado?'activo':''") {{item.nombre}}
                                span
                                    button.btn.btn-info.text-white.mr-2(@click="item.estado=!item.estado") 
                                    button.btn.btn-danger.text-white(@click="eliminarTarea(index)") x
                                

                    .d-flex.justify-content-center
                        button.btn.btn-primary.col-6(style="height:45px;" @click="agregarTarea") Agregar

</template>


<script>
    export default{
        data(){
            return{
                content : ''
            }
        },
        props:['nuevaTarea'],
        methods : {
            agregarTarea(){
                if(this.content){
                    this.nuevaTarea.push({nombre : this.content,estado : false});
                    this.content = '';
                }else{
                   alert("Ingresa datos")
                }
            },
            eliminarTarea(indice){
                this.nuevaTarea.splice(indice,1)
            }
        }
    }

</script>

<style lang="scss">
    .activo{
        color : #ccc;
        text-decoration:line-through; 
    }
</style>



